import React, { Component } from 'react';

class Individual extends Component {
    render() {
        return (
            <div id="ltab02" class="login_tab2">
                <div class="input_box01">
                    <div class="ip_box01"><input onChange= {this.props.change} id="find_name02" name="user_name02" type="text" placeholder=""/></div>
                    <div class="ib_box01">
                        <input onChange= {this.props.change} id="find_phoneNum02" name="user_phonenum02" type="text" placeholder="- ."/>
                        <button class="btn_medium_g"></button>
                    </div>
                    <div class="ip_box01"><input onChange= {this.props.change} id="find_certificationNum02" name="certification_num02" type="text" placeholder=" 6"/></div>
                </div>
            </div>
        );
    }
}

export default Individual;