import React, { Component } from 'react';
import LogoChecker from 'asset/images/logo_checker.png';
import IconComplete from 'asset/images/icon_complete.png';

class Main extends Component {
    render() {
        return (
            <article id="contentsWrap" class="login login03">
                <div class="box_contents">
                    <div class="box_logo">
                        <img src={LogoChecker} />
                    </div>
                    <div class="box_login">
                        <div class="box_mid">
                            <img src={ IconComplete } alt="" />
                            <p class="tit">  <br/>!</p>
                            <p class="txt">   .</p>
                            <div class="id_box">
                                <p>lhsworker@hyundai.com</p>
                            </div>
                        </div>
                        <div class="box_bottom">
                            <button class="btn_big_b"> </button>
                            <button class="btn_big"> </button>
                        </div>
                    </div>
                </div>
            </article>
        );
    }
}

export default Main;