import React from 'react';
import Subtest from './Subtest';

const Test = ({ todoId, fetching, userId,title, completed }) => {

    const ifCompleted = (completed === true) ? "" : "";
    /*
    const commentList= comments.map((x,idx)=> (
            <Subtest 
                name={x.name}
                body={x.body}
                idx={idx}
            />
        )
    );
    */

    return (
        <div>
              {todoId} <br></br>
             : {userId} <br></br>
            : {title} <br></br>
            ? : {ifCompleted}
         
        </div>
    );
};

export default Test;